body {
    padding: 20px;
    background-color: #282c34;
    font-family: sans-serif;
  }
  #container {
    display: flex;
    justify-content: center;
    color: white;
  }
.dashboardMain .header {
    display: inline-grid;
    justify-content: center;
    width: 700px;
}

.dashboardMain .header .row .box {
    background-color: lightgrey;
    width: 200px;
    height: 200px;
    margin: 50px 10px 10px 20px;
}

.box.list {
    float: left;
}

.box.group {
    float: right;
}

.dashboardMain .header .row .box img {
    margin: 10px 10px 10px 10px;
    height: 50%;
    width: 50%;
}

.dashboardMain .header .row .box h1 {
    margin: 10px 10px 10px 10px;
    color: black;
}

.dashboardMain .header .row .inactive h1 {
    color: grey;
}

.wishlistMain .header input {
    padding: 10px;
    font-size: 16px;
    border: 2px solid #FFF;
    width: 165px;
  }
  .wishlistMain .header button {
    padding: 10px;
    font-size: 16px;
    margin: 10px;
    margin-right: 0px;
    background-color: #0066FF;
    color: #FFF;
    border: 2px solid #0066FF;
  }
  .wishlistMain .header button:hover {
    background-color: #003399;
    border: 2px solid #003399;
    cursor: pointer;
  }
  .wishlistMain .theList {
    list-style: none;
    padding-left: 0;
    width: 250px;
  } 
  .wishlistMain .theList li {
    color: #333;
    background-color: rgba(255,255,255,.5);
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    list-style: none;
    transition: background-color .2s ease-out;
  }
  .wishlistMain .theList li:hover {
    background-color: pink;
    cursor: pointer;
  }
  ul.theList {
    padding: 0;
  }
